.mundo
{
    &__grid
    {
        display: grid;
        grid-template-columns: repeat(4, 30rem);
        column-gap: 4rem;
        @include scrollHorizontal;
        @include tablet 
        {
            @include grid(2, 2rem);
            overflow-x: hidden
        }

        @include desktop 
        {
            @include grid(4, 2rem);
        }
    }
}

.lugar
{
    &__imagen
    {
        
        img
        {
            border-radius: 1rem;
        }
    }

    &__parrafo
    {
        font-size: 1.8rem
    }
}