.destinos
{

    &__grid
    {
        @include grid(1, 2rem);

        @include tablet 
        {
            @include grid(2, 2rem);
        }

        @include desktop 
        {
            @include grid(4, 2rem);
        }
    }
}

.destino
{
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    gap: 1rem;
    overflow-x: hidden;
    
    &__imagen
    {
        width: 14rem;
    }

    &__nombre,
    &__distancia
    {
        margin: 0;
    }
}