.footer
{
    background-color: lighten($grisClaro, 7%);
    border-top: 1px solid lighten($gris, 40%);
    padding-top: 5rem;

    &__grid
    {
        display: flex;
        flex-direction: column;
        align-items: center;
        gap: 2rem;
        text-align: center;

        @include tablet 
        {
            @include grid(2, 4rem);
            text-align: left;
        }

        @include desktop 
        {
            @include grid(4, 4rem);
        }
    }

    &__menu
    {
        display: flex;
        flex-direction: column;
        gap: 1.5rem;
        text-align: center;

        @include tablet 
        {
            text-align: left;
        }
    }

    &__heading
    {
        font-weight: 700;
        font-size: 2rem;
        margin: 2rem 0 1rem 0;

        @include tablet 
        {
            margin: 0 0 1rem 0;
        }
    }

    &__enlace
    {
        color: $gris;
        margin: 0;
        font-size: 1.8rem;
    }

    &__barra
    {
        border-top: 1px solid lighten($gris, 40%);
        margin: 2rem auto 0 auto;
        padding: 2rem 0;
    }

    &__barranav
    {
        display: flex;
        flex-direction: column;
        align-items: center;
        gap: 2rem;

        @include tablet 
        {
            flex-direction: row;
        }
    }

    &__enlace--barra
    {

        &::before
        {
            content: '-';
        }
    }

    &__copyright
    {
        margin: 0 auto;
        padding: 1rem;
        text-align: center;
        border-top: 1px solid lighten($gris, 40%);
        text-transform: uppercase;
        font-weight: 600;
        font-size: 1.2rem;
        max-width: 60rem;
    }
}