.header
{
    background-image: url(../img/header.webp);
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    padding: 3rem 0 20rem 0;

    &__busqueda
    {
        margin-top: 5rem;
        text-align: center;

        @include tablet 
        {
            margin-top: 30rem;
        }
    }

    &__busqueda-boton
    {
        @include boton($blanco, $primario);
    }
}

.barra
{
    display: none;
    @include tablet 
    {
        display: flex;
        justify-content: space-between;
        align-items: center;
    }

    &__navegacion
    {
        display: flex;
        gap: 4rem;
    }

    &__link
    {
        color: $blanco;
    }

    &__nombre
    {
        color: $blanco;
        margin: 0;
    }
}

.formulario
{
    margin: 5rem auto 2rem auto;
    background-color: $blanco;
    border-radius: 5rem;
    box-shadow: 0px 3px 7px 0px $gris;
    padding: 3rem 0;

    @include desktop 
    {
        display: flex;
        justify-content: space-between;
        max-width: 90rem;
        padding: 0;
    }

    &__campo
    {
        padding: 1rem 2rem;
        display: flex;
        flex-direction: column;
        transition-property: background-color border-radius;
        transition-duration: .3s;

        &:nth-child(1)
        {
            @include desktop 
            {
                padding-left: 4rem;
            }
        }

        &:hover
        {
            background-color: #F0F0F0;
            border-radius: 5rem;
            box-shadow: 0px 6px 20px rgb(0 0 0 / 40%);
        }
    }

    &__label
    {
        font-size: 1.2rem;
        font-weight: 900;
    }

    &__input
    {
        border: none;
        width: 100%;
        color: $gris;
    }

    &__submit
    {
        display: block;
        height: 4rem;
        border: none;
        border-radius: 2rem;
        background-image: radial-gradient( circle, rgb(255, 56, 92) 0%, $secundario 100% );
        padding: 0 4rem;
        color: $blanco;
        text-transform: uppercase;
        font-size: 1.2rem;
        font-weight: 700;
    }
}