.experiencias
{

    &__grid
    {
        display: grid;
        grid-template-columns: repeat(3, 30rem);
        column-gap: 4rem;
        @include scrollHorizontal;

        @include desktop 
        {
            @include grid(3, 2rem);
            overflow-x: hidden;
        }
    }
}

.experiencia
{

    &__imagen
    {
        img
        {
            border-radius: 1rem;
        }
    }

    &__heading
    {
        font-weight: 700;
        font-size: 2rem;
    }

    &__parrafo
    {
        font-size: 1.8rem;
    }
}