.anfitrion
{
    color: $blanco;
    display: grid;
    grid-template-rows: repeat(2, 1fr);
    row-gap: 15rem;
    padding: 5rem;
    background-image: linear-gradient(to bottom, $negro 0%, $negro 50%, transparent 50% ), url(../img/anfitrion.webp);
    background-size: 100%, 60rem;
    background-repeat: no-repeat;
    background-position: center, center bottom;
    border-radius: 3rem;

    @include telefono 
    {
        background-size: 100%, 70rem;
        background-position: center, center 200%;
    }

    @include tablet 
    {
        grid-template-rows: unset;
        background-image: linear-gradient(to right, $negro 0%, $negro 45%, transparent 55% ), url(../img/anfitrion.webp);
        background-size: 100%, auto;
        background-position: center, 50% 30%;
        padding: 15rem 5rem;
    }

    @include desktop 
    {
        background-position: center, right center;
    }

    &__contenido
    {
        @include tablet 
        {
            max-width: 50%;
        }
    }

    &__texto
    {
        color: $blanco;
    }

    &__enlace
    {
        @include boton($blanco, $negro);
        border-radius: 1rem;
        padding-top: 1rem;
        padding-bottom: 1rem;
        text-align: center;
    }
}